import React from "react";

const SmsWork = () => {
    return (
        <div className="content-wrapper">
            <div className="sms-work-wrapper">
                <div className="poster">
                    <img src="static/img/stack/flight.png" className="flight-icon" alt="" />
                    <img src="static/img/stack/redbeanphp.png" className="redbeanphp-icon" alt="" />
                    <div className="card">
                        <img className="card-img" src="static/img/works/sms.png" alt="" />
                        <div className="card-description">
                            <a href="http://sms-panel.gherciu.gq" target="_blank" className="card-btn">
                                <img src="static/img/icons/see.svg" />
                                View demo
                            </a>
                        </div>
                    </div>
                </div>
                <div className="content">
                    <h2 className="content-text-heading">SMS-Panel.</h2>
                    <h3 className="content-text-subheading">
                        A dashboard for monitoring and viewing statistical data on sms sent through telephone providers.
                    </h3>
                    {/* <a target='_blank' href='' className='button-link -view'>View demo</a> */}
                </div>
            </div>
            <style jsx>{`
                .flight-icon {
                    max-width: 250px;
                    height: auto;
                    position: absolute;
                    top: 40px;
                    left: -90px;
                    transform: rotate(-90deg);
                }
                .redbeanphp-icon {
                    max-width: 350px;
                    height: auto;
                    position: absolute;
                    bottom: -50px;
                    right: -160px;
                    transform: rotate(-90deg);
                }
                .sms-work-wrapper {
                    display: flex;
                    justify-content: space-between;
                    margin-bottom: 0px;
                    align-items: center;
                }
                h3 {
                    margin-top: 10px;
                }
                .content {
                    max-width: 390px;
                }
                .content-text-heading {
                    text-align: right;
                }
                .content-text-subheading {
                    text-align: right;
                }
                .poster {
                    max-width: 560px;
                    width: 100%;
                    padding: 20px 0px;
                    display: flex;
                    justify-content: center;
                    position: relative;
                }
                .card {
                    max-width: 480px;
                    z-index: 2;
                }
                @media (max-width: 860px) {
                    .sms-work-wrapper {
                        flex-direction: column-reverse;
                    }
                    .content-text-heading {
                        text-align: center;
                    }
                    .content-text-subheading {
                        text-align: center;
                    }
                    .flight-icon {
                        display: none;
                    }
                    .redbeanphp-icon {
                        display: none;
                    }
                }
            `}</style>
        </div>
    );
};

export default SmsWork;
